<template>
	<div class="stock_wrap">
		<!--  -->
		<div class="stock_top">
			<el-input placeholder="" icon="search" :on-icon-click="handleIconClick" class="search_stock">
			</el-input>
			<el-button type="primary"  @click="dialogFormVisible = true"></el-button>
		</div>	

		<div class="stock_content">
			<el-table
			    ref="multipleTable"
			    class="tb-edit"
			    :data="stockData1.stockData"
			    border
			    tooltip-effect="dark"
			    style="width: 100%"
			     :default-sort = "{prop: 'operatingTime', order: 'descending',prop: 'stockNum',order:'ascending'}"
			      highlight-current-row>
			    <el-table-column
			      type="selection"
			      width="55">
			    </el-table-column>
			    <el-table-column
			      label=""
			      sortable prop="stockNum"
			      width="120"
			      header-align="center"
			      >
			      <template scope="scope">{{ scope.row.stockNum }}</template>
			    </el-table-column>
			    <el-table-column
			      prop="stockType"
			      label=""
			      sortable
			      width="120"
			      header-align="center">
			      <template scope="scope">			       
			      <span>{{ scope.row.stockType }}</span>
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="stockName"
			      label=""
			      width="120"
			      header-align="center">
			      <template scope="scope">			      
			      <span>{{ scope.row.stockName }}</span>
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="Poh"
			      label=""
			      width="120"
			      header-align="center">
			      <template scope="scope">			      
			      <span>{{ scope.row.Poh }}</span>
			      </template>
			    </el-table-column>
				<el-table-column
			      prop="unit"
			      label=""
			      width="120"
			      header-align="center">
			      <template scope="scope">  			    
			      <span>{{ scope.row.unit }}</span>
			      </template>
			    </el-table-column>
			    <el-table-column
			      label=""
			      show-overflow-tooltip
			      sortable prop="operatingTime"
			      header-align="center">
			        <template scope="scope">			               			
	        		<span style="margin-left: 10px"><el-icon name="time"></el-icon>{{ scope.row.operatingTime }}</span>
	        	    </template>
			    </el-table-column>
			    <el-table-column
			      prop="operator"
			      label=""		      
			      width="120"
			      header-align="center">
			      <template scope="scope">			     
			      <span>{{ scope.row.operator }}</span>
			      </template>
			    </el-table-column>
			    <el-table-column		      
			      label=""		      
			      show-overflow-tooltip
			      header-align="center">
			      <template scope="scope">			      
	        		<el-button @click="editStock(scope.$index, scope.row)" icon="edit"></el-button>        	
	        	  </template>
			    </el-table-column>
			</el-table>		
		</div>
		<!--  -->
		<div class="page">
			<el-pagination @size-change="stockSizeChange" @current-change="stockCurrentChange"
			:current-page.sync="stockcurrentPage" :page-size="12"
			layout="total, prev, pager, next, jumper" :total="400">
			</el-pagination>
		</div>	

		<!--  -->
		<el-dialog title="" :visible.sync="dialogFormVisible" size="700px">
		  <el-form :model="form">
		    <el-form-item label="">
		      <el-input v-model="form.stockNum" auto-complete="off"></el-input>
		    </el-form-item>
		    <el-form-item label="">
		      <el-input v-model="form.stockType" auto-complete="off" ></el-input>
		    </el-form-item>
		    <el-form-item label="">
		      <el-input v-model="form.stockName" auto-complete="off"></el-input>
		    </el-form-item>
		    <el-form-item label="">
		      <el-input v-model="form.Poh" auto-complete="off"></el-input>
		    </el-form-item>
		    <el-form-item label="">
		      <el-input v-model="form.unit" auto-complete="off"></el-input>
		    </el-form-item>
		     <el-form-item label="">
		      <el-date-picker type="date" placeholder="" v-model="form.operatingTime"  style="width: 220px">
			  </el-date-picker>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false"> </el-button>
		    <el-button type="primary" @click="AddStockData"></el-button>
		  </div>
		</el-dialog>

		<!--  -->
		<el-dialog :visible.sync="stockEditDialogVisible" title="" size="800px">
			<el-form :model="stockEditData">
		    	<el-form-item label="">
		      		<el-input v-model="stockEditData.stockNum" auto-complete="off"></el-input>
		    	</el-form-item>
		    	<el-form-item label="">
		      		<el-input v-model="stockEditData.stockType" auto-complete="off" ></el-input>
		    	</el-form-item>
		    	<el-form-item label="">
		      		<el-input v-model="stockEditData.stockName" auto-complete="off"></el-input>
		    	</el-form-item>
		    	<el-form-item label="">
		      		<el-input v-model="stockEditData.Poh" auto-complete="off"></el-input>
		    	</el-form-item>
		    	<el-form-item label="">
		      		<el-input v-model="stockEditData.unit" auto-complete="off"></el-input>
		    	</el-form-item>
		     	<el-form-item label="">
		      		<el-date-picker type="date" placeholder="" v-model="stockEditData.operatingTime"  style="width: 220px">
				</el-date-picker>
		    	</el-form-item>
		  	</el-form>
		  	<div slot="footer" class="dialog-footer">
		    	<el-button @click="stockEditDialogVisible = false"> </el-button>
		    	<el-button type="primary" @click="editStockSubmit"></el-button>
		  	</div>
		</el-dialog>
	</div>		
</template>

<style>
.stock_top {
	margin: 10px;
}
.search_stock{
	width: 180px;
}
.page{
	margin: 10px;
} 
</style>
<script>
import store from '../../store/index'
export default {
  	data: function() {
    	return {
      		stockData1:{
      			stockData:[]
      		},
      		stockList:store.state.stock.stockData,
      		// stockList:this.$store.state.stock.stockData,
      		stockcurrentPage:1,
      		dialogFormVisible: false,
      		stockEditDialogVisible: false,
			//
	  		form: { 				
				stockNum: '',
			    stockType: '',
			    stockName: '',
			    Poh:'',
			    unit:'',
			    operatingTime:'',
			    operator:''		        
			},
			//
			stockEditData: { 				
				stockNum: '',
			    stockType: '',
			    stockName: '',
			    Poh:'',
			    unit:'',
			    operatingTime:'',
			    operator:''		        
			},         	       	
			editStockIndex: "",
		    multipleSelection: [],
      	}   	
  	},
  	mounted:function(){
  		this.getList();
  	},
  	methods: {
  		//
    	handleIconClick(ev) {
      	console.log(ev);
    	},

      	// 
		toggleSelection(rows) {
        	if (rows) {
          		rows.forEach(row => {
            		this.$refs.multipleTable.toggleRowSelection(row);
          		});
        	} else {
          		this.$refs.multipleTable.clearSelection();
        	}
      	},
        handleSelectionChange(val) {
        	this.multipleSelection = val;
      	},
      	
      	// -pageSize 
		stockSizeChange(val) {
			console.log(` ${val} `);
		},
		// -currentPage 
		stockCurrentChange(val) {
			console.log(`: ${val}`);
		},

      	// 
		AddStockData(){
			var addThis=this;
			// 
	        let oldDate = addThis.form.operatingTime;
	        let year = new Date(oldDate).getFullYear();
	        let month = new Date(oldDate).getMonth()+1;
	        let day = new Date(oldDate).getDate();
	        if (month < 10) { month = "0" + month};
	        if (day < 10) { day = "0" + day};
	        addThis.form.operatingTime = year + "-" + month + "-" + day;
			addThis.stockData1.stockData.push(addThis.form);
			addThis.dialogFormVisible=false
		},

		// 
    	getList:function(){
		    var me = this;
			this.$http.get('../../static/dataJson/stock.json').then(function(response){
					console.log(response);
					console.log("stock()json",response.data);
					me.stockData1 = response.data;
					me.stockData1.stockData=me.stockList;
				}, function(response){
					alert("")
			});				
		},
		// :
		editStock(index, row) {
			console.log("");
			this.stockEditDialogVisible = true;
			// row
			this.stockEditData = JSON.parse(JSON.stringify(row));
			this.editStockIndex = index;
		},
		// 
		editStockSubmit(){
			console.log("");
			// 
			let editingId = this.editStockIndex;
			// 
	        let oldDate = this.stockEditData.operatingTime;
	        console.log(oldDate)
	        let year = new Date(oldDate).getFullYear();
	        let month = new Date(oldDate).getMonth()+1;
	        let day = new Date(oldDate).getDate();
	        if (month < 10) { month = "0" + month};
	        if (day < 10) { day = "0" + day};
	        this.stockEditData.operatingTime= year + "-" + month + "-" + day;
	        console.log("day",day);
	        // this.stockEditData.operatingTime =this.datepickers.date;
			// 
			this.stockData1.stockData.splice(editingId,1,this.stockEditData);
			this.stockEditDialogVisible = false;
			this.stockEditData = {
				stockNum: '',
			    stockType: '',
			    stockName: '',
			    Poh:'',
			    unit:'',
			    operatingTime:'',
			    operator:''	
			}       	
		},
  	}
}
</script>


